	module.exports = {"data":{"markdownRemark":{"headings":[{"value":"Exploring audio visual design through an interactive album","depth":1},{"value":"The Album","depth":2},{"value":"The Experience","depth":2}],"html":"<h1>Exploring audio visual design through an interactive album</h1>\n<p>ORA is a multi-dimensional music project by Rosy Glow. The project consists of a 4-song EP with an accommodating web application that allows users to remix different musical components of the album within their browser. The concept behind the project was to blur the lines between the user and the artist and generate an interface where anyone can experience the process of producing music. </p>\n<h2>The Album</h2>\n<p>The album itself explores themes of nostalgia , sonically blending organic and electronic sounds to create a newfound </p>\n<h2>The Experience</h2>\n<p>The primary goal of ORA was to make the process of music making accessible. I used this album as a method for creating a unified audio/visual language that could be applied to an interactive interface. I used elements from the art direction of the album artwork to design a module-based web application</p>","frontmatter":{"title":"ORA","graduate":"Aaron Mohr","category":"UI/UX","category2":"Web Design","category3":null,"image":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1.5,"src":"/the-next-website/static/ora-thumb-5108c323640b10bcedb8a8234b74c8a2-a7a62.jpg","srcSet":"/the-next-website/static/ora-thumb-5108c323640b10bcedb8a8234b74c8a2-5a1a9.jpg 375w,\n/the-next-website/static/ora-thumb-5108c323640b10bcedb8a8234b74c8a2-0908c.jpg 750w,\n/the-next-website/static/ora-thumb-5108c323640b10bcedb8a8234b74c8a2-a7a62.jpg 1500w,\n/the-next-website/static/ora-thumb-5108c323640b10bcedb8a8234b74c8a2-83d7d.jpg 2000w","sizes":"(max-width: 1500px) 100vw, 1500px"}}}}}},"pathContext":{"slug":"/work/ora/","name":"/ORA/"}}

